     left: 0px;\r\n          right: auto;\r\n        }\r\n      }\r\n    }\r\n\r\n    &[view-mode=\"date\"] .ddtp-body > .ddtp-days {\r\n      display: block;\r\n    }\r\n\r\n    &[view-mode=\"datetime\"] {\r\n      .ddtp-body > .ddtp-times,\r\n      .ddtp-body > .ddtp-days {\r\n        display: block;\r\n      }\r\n    }\r\n\r\n    &[view-mode=\"time\"] {\r\n      .ddtp-body > .ddtp-times {\r\n        display: block;\r\n      }\r\n\r\n      .ddtp-header {\r\n        display: none;\r\n      }\r\n    }\r\n\r\n    &[view-mode=\"year\"] {\r\n      .ddtp-body > .ddtp-years {\r\n        display: flex;\r\n      }\r\n\r\n      .ddtp-header-month {\r\n        display: none;\r\n      }\r\n    }\r\n\r\n    &[view-mode=\"month\"] {\r\n      .ddtp-body > .ddtp-months {\r\n        display: flex;\r\n      }\r\n\r\n      .ddtp-header-month {\r\n        display: none;\r\n      }\r\n    }\r\n  }\r\n\r\n  .ddtp-header {\r\n    padding: 2px 5px 10px;\r\n    line-height: 25px;\r\n    height: 25px;\r\n    vertical-align: middle;\r\n\r\n    .ddtp-header-year {\r\n      font-weight: bold;\r\n      margin: 0px 10px 0px 0px;\r\n      cursor: pointer;\r\n    }\r\n\r\n    .ddtp-header-month {\r\n      font-weight: bold;\r\n      margin: 0px 10px 0px 0px;\r\n      vertical-align: top;\r\n      cursor: pointer;\r\n    }\r\n\r\n    .ddtp-date-move {\r\n      margin-left: auto;\r\n      vertical-align: top;\r\n      float: right;\r\n      direction: ltr;\r\n\r\n      .ddtp-move-btn {\r\n        text-decoration: none;\r\n        font-weight: bold;\r\n        display: inline-block;\r\n        height: 24px;\r\n\r\n        > svg {\r\n          fill: var(--color);\r\n        }\r\n\r\n        &:hover {\r\n          background-color: var(--button-hover-color);\r\n        }\r\n      }\r\n\r\n      &::after {\r\n        content: \"\";\r\n        clear: both;\r\n      }\r\n    }\r\n  }\r\n\r\n  .ddtp-body {\r\n    margin: -2px -10px;\r\n    font-size: 13px;\r\n\r\n    > * {\r\n      display: none;\r\n    }